import Vue from 'vue'
import router from './router'
import store from './vuex/store'
import App from './App'

require('assets/js/jquery.ta')

FastClick.attach(document.body)

router.beforeEach((to, from, next) => {
	let vlink = to.path;
	let path = vlink.split('/')[1]; // 截取一级路由
	if(!path){
		router.replace(store.getters.getSidebar[0].link)
		return false;
	}
	path = '/'+path;
	store.getters.getSidebar.forEach(function(nav) {
		nav.active = false;
		if(nav.link == path) {
			nav.active = true;
		}
		if(nav.child) {
			nav.child.forEach(function(child) {
				child.active = false;
				if(child.link == vlink) {
					nav.active = true;
					child.active = true;
				}
			})
		}
	})
	$.AMUI.progress.start();
	next()
})

router.afterEach(transition => {
	$.AMUI.progress.done();
})

new Vue({
	router,
	store,
	el: '#app',
	template: '<App/>',
	components: {
		App
	},
	created () {
		const self = this;
		let path = self.$route.path.split('/')[1];
		if(path) {
			// 始终返回一级路由
			self.$router.replace('/'+path)
		}
		$.AMUI.progress.start()
	},
	mounted() {
		// 页面加载完毕，清除 load
		setTimeout(function() {
			$.AMUI.progress.done(true);
			$('.ta-loader').addClass('animated fadeOut').one('webkitAnimationEnd mozAnimationEnd MSAnimationEnd oanimationend animationend', function() {
				$(this).remove();
			});
		}, 50);
	}
}).$mount('#app')




