<template>

	<section class="ta-content-wrapper">
		<div>
			<a href="javascript:;" class="am-btn am-btn-default" @click="historyPrev">
				<i class="iconfont icon-fanhui"></i>
				返回上一页
			</a>
		</div>
		
		<router-view></router-view>
	</section>

</template>

<script>
	export default {
		name: 'Wrapper',
		methods: {
			historyPrev () {
				this.$router.go(-1)
			}
		}
	}
</script>


