<template>

	<div id="app" class="ta-app" :class="[Config.Unfold ? 'ta-unfold' : 'ta-fold', 'ta-theme-'+Config.Settings.theme]">
		<my-head></my-head>

		<div class="ta-content">

			<my-sidebar></my-sidebar>
			<div class="ta-overlay" :class="{'active': Config.Unfold}"></div>

			<my-wrapper></my-wrapper>
		</div>

		<my-toast></my-toast>
		<my-settings></my-settings>
	</div>

</template>

<script>
	import {
		mapGetters,
		mapActions
	} from 'vuex'

	import MyHead from 'components/Head'
	import MySidebar from 'components/Sidebar'
	import MySettings from 'components/Settings'
	import MyToast from 'components/Toast'
	import MyWrapper from 'components/Wrapper'

	export default {
		name: 'App',
		computed: {
			...mapGetters({
				Config: 'getConfig'
			})
		},
		components: {
			MyHead,
			MySidebar,
			MySettings,
			MyToast,
			MyWrapper
		},
		methods: {
			...mapActions({
				fold: 'Fold'
			})
		},
		mounted () {
			var self = this;
			$('.ta-overlay').on('click touchstart', function(){ // 防止滑动时不收起左侧栏
				self.fold()
			})
		}
	}
</script>

<style lang="less">
	@import "assets/css/ta.less";
	@import "assets/css/main.less";
</style>


